import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import SEO from "../components/seo"

const ContactPage = () => (
  <Layout>
    <SEO title="Contact Us" />
    <div class="col-md-4" id="content-desktop">
      <Sidebar />
    </div>

    <div class="col-md-8">
      <br />

      <div class="alert alert-secondary" role="alert">
        <h2>
          تواصل معنا <i class="fas fa-mail-bulk"></i>
        </h2>
      </div>
      <p>يمكنك التواصل معنا فقط قم بمليء هذة الإستمارة</p>
      <hr />
      <b>
        <div class="" dir="rtl" id="afterSubmit"></div>
      </b>
      <div dir="rtl">
        <form
          id="contact"
          target="_blank"
          action="https://getform.io/f/b89796d9-415d-4216-9bb7-e6282ee1e298"
          method="POST"
          name="contact"
          onSubmit={() => {
            let form = document.getElementById("contact")
            let element = document.getElementById("afterSubmit")
            element.setAttribute("class", "alert alert-success")
            element.innerHTML = "شكرا لك : تم إرسال رسالتك بنجاح"
            form.reset()
          }}
        >
          <div className="form-group">
            <input
              type="text"
              name="name"
              className="form-control"
              id="inputName"
              placeholder="اسمك"
            />
          </div>
          <div className="form-group">
            <input
              type="email"
              name="email"
              className="form-control"
              id="inputEmail1"
              aria-describedby="emailHelp"
              placeholder=" البريد الاليكتروني"
              required
            />

            <small id="inputEmail1" class="form-text text-muted">
              * أرفق إيميلك لكي نتمكن من التواصل معك
            </small>
          </div>
          <div className="form-group">
            <textarea
              class="form-control"
              type="msg"
              id="inputMassage"
              rows="5"
              name="msg"
              placeholder="أكتب رسالتك هنــا .... "
              required
            />
          </div>
          <button
            type="submit"
            value="Submit now"
            className="btn btn-primary btn-sm"
          >
            <i class="far fa-paper-plane"></i> أرســـل
          </button>
        </form>
      </div>
      <hr />
      <p>نحن شغوفين للسماع منك وسوف نقوم بالتواصل معك في أسرع وقت ممكن </p>
      <Link to="/" class="btn btn-secondary">
        <i class="far fa-arrow-alt-circle-left"></i> العودة للخلف
      </Link>
      <br />
      <br />
    </div>
  </Layout>
)

export default ContactPage
